@import "../../../css-config/mixins.scss";

.shop-by-category {
  margin: 20px 0;
  @include md{
    margin: 50px 0;
  }
  .categories {
    display: flex;
    flex-flow: wrap;
    gap: 10px;
    .category {
    
      width: calc(48% - 5px);
      cursor: pointer;
      overflow: hidden;
      @include md{
        width:calc(25% - 10px);
      }
      img {
        width: 100%;
        display: block;
        transition: all ease 0.3s;
      }
      &:hover{
        img{
          transform: scale(1.2);
        }
      }
    }
  }
}
